import * as React from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import { registerUser } from "../actions/action";
import {useHistory} from "react-router-dom";

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit" href="https://mui.com/">
        
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
      <div>
           '' .
      </div>
    </Typography>
  );
}

const theme = createTheme();

export default function Signup({match}) {

  const [value, setValue] = React.useState('male');
  const handleChange = (event) => {
    setValue(event.target.value);
  };

  // const location=useLocation();
  // React.useEffect(() => {
  //   console.log(location);
  //   console.log("2  ");
  // }, [location])

  const history = useHistory();

  function isMale(a){
    if(a==='female') return 1;
    else return 0;
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    let body = {
      id: data.get('id'),
      name: data.get('name'),
      password: data.get('password'),
      email: data.get('email'),
      phone: data.get('phone'),
      sex: isMale(data.get('sex')),
      age: data.get('age')
    };
    if (!data.get('id') || !data.get('email') 
    || !data.get('password') || !data.get('name')
    || !data.get('phone') || !data.get('sex')
    || !data.get('age')) {
      alert("  !");
    } else {
      registerUser(body)
        .then((response) => {
          if (response.resultCode===0) {
            console.log(" ");
            history.push('/Community/login');
            window.location.reload();
          } else if(response.resultCode===1) {
            alert('  .');
          } else{
            alert(' ')
          }
        })
    }
  };

  
  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <div className="Card1">
            <div className="c1image">
              <img src={require('../assets/images/jbmb_logo.png')} alt='logo' />
            </div>
          </div>
          <Typography component="h5" variant="h5">
            
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="id"
                  name="id"
                  required
                  fullWidth
                  id="id"
                  label="ID"
                  autoFocus
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="name"
                  label=""
                  name="name"
                  autoComplete="name"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label=""
                  name="email"
                  autoComplete="email"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="PW"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                />
              </Grid>              
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="phone"
                  label=""
                  type="phone"
                  id="phone"
                  autoComplete="phone"
                />
              </Grid>  
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="age"
                  label=""
                  type="age"
                  id="age"
                  autoComplete="age"
                />
              </Grid>
              <Grid item xs={12}>
               <FormControl>
                <FormLabel id="demo-controlled-radio-buttons-group"></FormLabel>
                <RadioGroup              
                  row
                  aria-labelledby="demo-controlled-radio-buttons-group"
                  name="sex"
                  value={value}
                  onChange={handleChange}
                >
                  <FormControlLabel value="male" control={<Radio />} label="" />
                  <FormControlLabel value="female" control={<Radio />} label="" />
                </RadioGroup>
               </FormControl>
              </Grid>
            </Grid>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <Button
                  type="button"
                  fullWidth
                  variant="contained"
                  href="/Community/login"
                  sx={{ mt: 3, mb: 2 }}
                >
                  
                </Button>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"                  
                  sx={{ mt: 3, mb: 2 }}
                >
                  
                </Button>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 5 }} />
      </Container>
    </ThemeProvider>
  );
}